import Vue from 'vue';

/**
 * ,      key => value.
 *
 * @param state
 * @param data
 * @constructor
 */
export const UPDATE = (state, data) => {
    Object.entries(data).forEach(([k, v]) => {
        Vue.set(state, k, v);
    });
};

/**
 *        key => value.
 *
 * @param commit
 * @param data
 * @returns {*}
 */
export const update = ({ commit }, data) => commit('UPDATE', data);
