import {UPDATE, update} from '../utils';
import api from '@/api';

const state = {
    list: [],
    storyStatuses: [
        {id: 1, value: ''},
        {id: 2, value: ' '},
    ],
    readingTime: [
        {time: 300, text: '5 '},
        {time: 600, text: '10 '},
        {time: 900, text: '15 '},
    ],
};

const getters = {};

const actions = {
    update,
    async updateCategoryOrder({}, {categoryId, stories}) {
        return new Promise(resolve => {
            api.put(`/categories/${categoryId}/stories`, {stories})
                .then(response => {
                    resolve(response === 'success' ? 'success' : {error: 'unknown error'});
                })
                .catch(error => {
                    resolve({error});
                });
        });
    },
};

const mutations = {
    UPDATE,
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
};
