import Vue from 'vue';
import Vuex from 'vuex';

import permissions from '@/store/modules/permissions';
import stories from '@/store/modules/stories';
import users from '@/store/modules/users';
import players from '@/store/modules/players';
import firebase from '@/store/modules/firebase';

import createPersistedState from 'vuex-persistedstate';
import {update, UPDATE} from '@/store/utils';
import api from '@/api';

Vue.use(Vuex);

const state = {
    routerHasHistory: false,
    configs: Object
};

const getters = {};

const mutations = {
    UPDATE,
    
    UPDATE_CONFIG(state, config) {
        Object.entries(config).forEach(([key, value]) => {
            Vue.set(state.configs, key, value);
        });
    }
};

const actions = {
    update,
    async getConfigs({commit, state}) {
        return new Promise(resolve => {
            if (Object.keys(state.configs).length) {
                resolve(state.configs);
                return;
            }
            
            api.fetch('/config')
                .then(configs => {
                    commit('UPDATE', {configs});
                    resolve(configs);
                })
                .catch(error => {
                    resolve({error});
                });
        });
    },
    async saveConfig({commit}, data) {
        return new Promise(resolve => {
            api.patch('/config', data)
                .then(config => {
                    commit('UPDATE_CONFIG', config);
                    resolve(config);
                })
                .catch(error => {
                    resolve({error});
                });
        });
    }
};

const modules = {
    permissions,
    stories,
    users,
    players,
    firebase
};

const store = new Vuex.Store({
    namespaced: true,
    name: 'store',
    state,
    getters,
    actions,
    mutations,
    modules,
    plugins: [createPersistedState({
        paths: [
            'firebase.chaptersSortingOrder',
        ],
    })],
});

if (module.hot) {
    module.hot.accept([
        './modules/permissions',
        './modules/stories',
        './modules/users',
        './modules/players',
        './modules/firebase',
    ], () => {
        const newPermissionsModule = require('./modules/permissions').default;
        const newStoriesModule = require('./modules/stories').default;
        const newUsersModule = require('./modules/users').default;
        const newPlayersModule = require('./modules/players').default;
        const newFirebaseModule = require('./modules/firebase').default;
        
        store.hotUpdate({
            modules: {
                permissions: newPermissionsModule,
                stories: newStoriesModule,
                users: newUsersModule,
                players: newPlayersModule,
                firebase: newFirebaseModule,
            },
        });
    });
}

export default store;
