import Vue from 'vue';

import {UPDATE, update} from '../utils';
import api from '@/api';
import credentials from "@/json/google-services.json";
import {initializeApp} from 'firebase/app';
import {getAuth, GoogleAuthProvider, onAuthStateChanged, signInWithPopup, signOut} from 'firebase/auth';
import {fetchAndActivate, getAll, getRemoteConfig} from 'firebase/remote-config';
import {clone} from '@/utils';

const FirebaseConfig = {
    apiKey: credentials.client[0].api_key[0].current_key,
    authDomain: `${credentials.project_info.project_id}.firebaseapp.com`,
    databaseURL: credentials.project_info.firebase_url,
    projectId: credentials.project_info.project_id,
    storageBucket: credentials.project_info.storage_bucket,
    appId: '1:1047356254397:android:517a1e9706b5a2042aefd4'
};
const FetchIntervalMillis = 1000;

const sortDesc = (a, b) => {
    if (a > b) return -1;
    if (a < b) return 1;
    return 0;
};

const state = {
    userEmail: '',
    isInitialized: false,
    accessToken: '',
    chaptersSortingOrder: {},
    config: {
        Stories: null,
        Categories: null,
        DailyBonuses: null,
        Prices: null,
        Products: null,
        EconomyConfig: null,
        Genres: null,
        Currencies: null
    }
};

const getters = {
    getDefaultStoryReward: state => {
        return [{
            currency_id: state.config.EconomyConfig.story_reward_currency_id,
            value: state.config.EconomyConfig.story_reward_currency_value
        }];
    },
    getNewStoryID: state => {
        const idsDesc = state.config.Stories.stories.map(data => data.id).sort(sortDesc);
        return idsDesc[0] + 1;
    },
    getNewChapterID: state => {
        const allChaptersIds = [];
        state.config.Stories.stories.forEach(storyData => {
            storyData.chapters.forEach(chapterData => allChaptersIds.push(chapterData.id));
        });
        return allChaptersIds.sort(sortDesc)[0] + 1;
    },
    getNewEntityID: state => entityName => {
        if (!state.config.hasOwnProperty(entityName)) {
            alert('FirebaseModule::getNewEntityID Invalid entity name');
            return 0;
        }
        const idsDesc = state.config[entityName].map(data => data.id).sort(sortDesc);
        return idsDesc[0] + 1;
    },
    getNewPriceID: state => {
        const idsDesc = state.config.Prices.prices.map(price => price.id).sort(sortDesc);
        return idsDesc[0] + 1;
    },
    getNewProductID: state => {
        const idsDesc = state.config.Products.products.map(product => product.id).sort(sortDesc);
        return idsDesc[0] + 1;
    },
    getStoryById: state => storyId => state.config.Stories.stories.find(story => story.id === storyId),
    getStoryChapters: state => storyId => {
        const storyConfig = state.config.Stories.stories.find(story => story.id === parseInt(storyId, 10));
        return storyConfig ? storyConfig.chapters : [];
    },
    getChapterById: state => (storyId, chapterId) => {
        const story = state.config.Stories.stories.find(story => story.id === storyId);
        
        if (story) {
            const chapter = story.chapters.find(chapter => chapter.id === chapterId);
            return chapter || false;
        }
        
        return false;
    },
    getGenreById: state => id => state.config.Genres.find(genre => genre.id === id),
    getStoryCategories: state => storyId => {
        const storyConfig = state.config.Stories.stories.find(story => story.id === parseInt(storyId, 10));
        const storyCategories = storyConfig ? storyConfig.categories : [];
        let categoriesData = [];
        
        storyCategories.forEach(categoryData => {
            const config = state.config.Categories.find(category => category.id === categoryData.id);
            if (config) {
                categoriesData.push({
                    id: config.id,
                    name: config.name
                });
            }
        });
    
        return categoriesData;
    },
    getCategoryById: state => id => state.config.Categories.find(category => category.id === id),
    getStoryGenres: state => storyId => {
        const storyConfig = state.config.Stories.stories.find(story => story.id === storyId);
        return storyConfig ? storyConfig.genres : [];
    },
    getNextChapterOrder: state => storyId => {
        const story = state.config.Stories.stories.find(story => story.id === storyId);
        let order = 0;
        
        if (story && story.chapters && story.chapters.length) {
            story.chapters.forEach(chapter => {
                if (chapter.order > order) order = chapter.order;
            });
        }
        return order + 1;
    },
    getCurrencyName: state => id => {
        const config = state.config.Currencies.find(currency => currency.id === id);
        return config ? config.name : '';
    },
    getCurrencyById: state => id => {
        const config = state.config.Currencies.find(currency => currency.id === id);
        return config || {};
    },
    getMultiProductsList: state => {
        const list = [];
        Object.values(state.config.Products.products).forEach(data => {
            if (data.rewards.length !== 1) list.push(data);
        });
        return list;
    },
    getSingleProductsList: state => {
        const list = {};
        Object.values(state.config.Products.products).forEach(data => {
            if (data.rewards.length === 1) {
                if (!list[data.rewards[0].id]) list[data.rewards[0].id] = [];
                list[data.rewards[0].id].push(data);
            }
        });
        return list;
    }
};

const actions = {
    update,
    async getConfig({state, commit}) {
        return new Promise(resolve => {
            if (state.isInitialized) {
                resolve(state.config);
                return;
            }
    
            const firebaseApp = initializeApp(FirebaseConfig);
            const auth = getAuth();
            const provider = new GoogleAuthProvider().addScope('https://www.googleapis.com/auth/cloud-platform');
    
            onAuthStateChanged(auth, (user) => {
                const fetchConfig = () => {
                    const remoteConfig = getRemoteConfig(firebaseApp);
                    remoteConfig.settings.minimumFetchIntervalMillis = FetchIntervalMillis;
                    remoteConfig.settings.fetchTimeoutMillis = FetchIntervalMillis;
                    fetchAndActivate(remoteConfig).then(_ => {
                        const config = getAll(remoteConfig);
                        Object.keys(config).forEach(key => {
                            try {
                                config[key] = JSON.parse(config[key].asString());
                            } catch (e) {
                            }
                        });
                        commit('UPDATE', {
                            isInitialized: true,
                            config
                        });
                        resolve(config);
                    }).catch(error => {
                        resolve({error});
                    });
                };
        
                if (user) {
                    commit('UPDATE', {accessToken: user.accessToken, userEmail: user.email});
                    fetchConfig();
                } else {
                    signInWithPopup(auth, provider)
                        .then(result => {
                            const credential = GoogleAuthProvider.credentialFromResult(result);
                            commit('UPDATE', {accessToken: credential.accessToken, userEmail: result.user.email});
                            fetchConfig();
                        })
                        .catch(error => {
                            resolve({error});
                        });
                }
            });
        });
    },
    async updateConfig({state}) {
        return new Promise(resolve => {
            const config = {parameters: {}};
            
            Object.keys(state.config).forEach(key => {
                let value;
                
                if (typeof state.config[key] == 'object')
                    value = JSON.stringify(state.config[key]);
                else
                    value = state.config[key];
                
                config.parameters[key] = {defaultValue: {value}};
            });
            api.put('', config)
                .then(_ => {
                    resolve({success: true});
                })
                .catch(error => resolve({error}));
        });
    },
    async saveStory({commit, dispatch}, {storyData, isNewStory}) {
        return new Promise(resolve => {
            commit('SAVE_STORY', storyData);
            dispatch('updateConfig')
                .then(response => {
                    if (isNewStory)
                        resolve({...response, redirectStory: storyData.id});
                    else
                        resolve(response);
                })
                .catch(error => resolve(error));
        });
    },
    async setStoryChapterPublishState({commit, dispatch}, data) {
        return new Promise(resolve => {
            commit('SET_STORY_CHAPTER_PUBLISH_STATE', data);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async saveStoryChapter({commit, dispatch}, {storyId, chapterData}) {
        return new Promise(resolve => {
            commit('SAVE_STORY_CHAPTER', {storyId, chapterData});
            commit('ADD_CHAPTER_SORTING_ORDER', {storyId, chapterId: chapterData.id, commit});
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async updateChapterSortingOrder({commit, dispatch}, data) {
        return new Promise(resolve => {
            commit('UPDATE_CHAPTERS_SORTING_ORDER', data);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async saveCurrency({commit, dispatch}, currencyData) {
        return new Promise(resolve => {
            commit('SAVE_CURRENCY', currencyData);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async saveCategory({commit, dispatch}, categoryData) {
        return new Promise(resolve => {
            commit('SAVE_CATEGORY', categoryData);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async saveGenre({commit, dispatch}, genreData) {
        return new Promise(resolve => {
            commit('SAVE_GENRE', genreData);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async updateEconomyConfig({commit, dispatch}, data) {
        return new Promise(resolve => {
            commit('UPDATE_ECONOMY_CONFIG', data);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async updateFakeStories({commit, dispatch}, data) {
        return new Promise(resolve => {
            commit('UPDATE_ECONOMY_CONFIG', data);
            commit('UPDATE_FAKE_STORIES', data);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async savePrice({commit, dispatch}, priceData) {
        return new Promise(resolve => {
            commit('SAVE_PRICE', priceData);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async updateStoriesRecommendedOrder({commit, dispatch}, sortOrder) {
        return new Promise(resolve => {
            commit('UPDATE_STORIES_RECOMMENDED_ORDER', sortOrder);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async saveProduct({commit, dispatch}, productData) {
        return new Promise(resolve => {
            commit('SAVE_PRODUCT', productData);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async deleteProduct({commit, dispatch}, productId) {
        return new Promise(resolve => {
            commit('DELETE_PRODUCT', productId);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async updateDailyBonusesConfig({commit, dispatch}, config) {
        return new Promise(resolve => {
            commit('UPDATE_DAILY_BONUSES_CONFIG', config);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async updateProductsOrder({commit, dispatch}, order) {
        return new Promise(resolve => {
            commit('UPDATE_PRODUCTS_ORDER', order);
            dispatch('updateConfig')
                .then(response => resolve(response))
                .catch(error => resolve(error));
        });
    },
    async signOut({commit}) {
        return new Promise(resolve => {
            const auth = getAuth();
            signOut(auth)
                .then(response => {
                    commit('UPDATE', {userEmail: '', accessToken: '', isInitialized: false});
                    resolve(response);
                })
                .catch(error => resolve(error));
        });
    }
};

const mutations = {
    UPDATE,
    SET_STORY_CHAPTER_PUBLISH_STATE(state, {storyId, chapterId, isPublished}) {
        const stories = clone(state.config.Stories.stories);
        const storyIndex = stories.findIndex(story => story.id === storyId);
        if (storyIndex === -1) return;
        
        const chapterIndex = stories[storyIndex].chapters.findIndex(chapter => chapter.id === chapterId);
        if (chapterIndex === -1) return;
        stories[storyIndex].chapters[chapterIndex].is_published = isPublished;
    
        //    -           
        const lastPublishedChapterId = stories[storyIndex].last_chapter_id;
        const chapterData = stories[storyIndex].chapters.find(chapter => chapter.id === lastPublishedChapterId);
        stories[storyIndex].is_published = typeof chapterData !== 'undefined';
        
        Vue.set(state.config.Stories, 'stories', stories);
    },
    ADD_STORY_GENRE(state, {storyId, genreData}) {
        const stories = clone(state.config.Stories.stories);
        const storyIndex = stories.findIndex(story => story.id === storyId);
        if (storyIndex === -1) return;
        
        const genreIndex = stories[storyIndex].genres.findIndex(g => g.id === genreData.id);
        if (genreIndex > -1) return;
    
        stories[storyIndex].genres.push(genreData);
        Vue.set(state.config.Stories, 'stories', stories);
    },
    SAVE_STORY(state, storyData) {
        const stories = clone(state.config.Stories.stories);
        const storyIndex = stories.findIndex(story => story.id === storyData.id);
        if (storyIndex > -1) stories[storyIndex] = storyData;
        else stories.push(storyData);
        Vue.set(state.config.Stories, 'stories', stories);
    },
    SAVE_STORY_CHAPTER(state, {storyId, chapterData}) {
        const stories = clone(state.config.Stories.stories);
        const storyIndex = stories.findIndex(story => story.id === storyId);
        if (storyIndex === -1) return;
        
        const chapterIndex = stories[storyIndex].chapters.findIndex(chapter => chapter.id === chapterData.id);
        if (chapterIndex > -1)
            Object.keys(chapterData).forEach(key => stories[storyIndex].chapters[chapterIndex][key] = chapterData[key]);
        else
            stories[storyIndex].chapters.push(chapterData);
        Vue.set(state.config.Stories, 'stories', stories);
    },
    ADD_STORY_TO_CATEGORY(state, {storyId, categoryId}) {
        const stories = clone(state.config.Stories.stories);
        const storyIndex = stories.findIndex(story => story.id === storyId);
        if (storyIndex === -1) return; //   
        //todo   order
        stories[storyIndex].categories.push({id: categoryId, order: 0});
        Vue.set(state.config.Stories, 'stories', stories);
        
        const categories = clone(state.config.Stories.categories);
        const categoryIndex = categories.findIndex(category => category.id === categoryId);
        if (categoryIndex === -1) return;
        categories[categoryIndex].stories.push(storyId);
        Vue.set(state.config.Stories, 'categories', categories);
    },
    DELETE_STORY_FROM_CATEGORY(state, {storyId, categoryId}) {
        const stories = clone(state.config.Stories.stories);
        let storyIndex = stories.findIndex(story => story.id === storyId);
        if (storyIndex === -1) return; //   
        let categoryIndex = stories[storyIndex].categories.findIndex(category => category.id === categoryId);
        if (categoryIndex === -1) return;
        stories[storyIndex].categories.splice(categoryIndex, 1);
        Vue.set(state.config.Stories, 'stories', stories);
        
        const categories = clone(state.config.Stories.categories);
        categoryIndex = categories.findIndex(category => category.id === categoryId);
        if (categoryIndex === -1) return;
        storyIndex = categories[categoryIndex].stories.indexOf(storyId);
        if (storyIndex === -1) return;
        categories[categoryIndex].stories.splice(storyIndex, 1);
        Vue.set(state.config.Stories, 'categories', categories);
    },
    DELETE_STORY_GENRE(state, {storyId, genreId}) {
        const stories = clone(state.config.Stories.stories);
        const storyIndex = stories.findIndex(story => story.id === storyId);
        if (storyIndex === -1) return;
    
        const genreIndex = stories[storyIndex].genres.findIndex(genre => genre.id === genreId);
        if (genreIndex === -1) return;
    
        stories[storyIndex].genres.splice(genreIndex, 1);
        Vue.set(state.config.Stories, 'stories', stories);
    },
    SET_CHAPTER_DEFAULT_REWARD(state, {storyId, chapterId}) {
        const stories = clone(state.config.Stories.stories);
        const storyIndex = stories.findIndex(story => story.id === storyId);
        if (storyIndex === -1) return;
        
        const chapterIndex = stories[storyIndex].chapters.findIndex(chapter => chapter.id === chapterId);
        if (chapterIndex === -1) return;
        
        stories[storyIndex].chapters[chapterIndex].rewards = [{
            currency_id: state.config.EconomyConfig.chapter_reward_currency_id,
            value: state.config.EconomyConfig.chapter_reward_currency_value,
        }];
        Vue.set(state.config.Stories, 'stories', stories);
    },
    UPDATE_CHAPTERS_SORTING_ORDER(state, {storyId, order}) {
        const stories = clone(state.config.Stories.stories);
        const storyIndex = stories.findIndex(story => story.id === storyId);
        if (storyIndex === -1) return;
        
        order.forEach((chapterId, index) => {
            const chapterIndex = stories[storyIndex].chapters.findIndex(chapterData => chapterData.id === chapterId);
            if (chapterIndex > -1) stories[storyIndex].chapters[chapterIndex].order = index + 1;
        });
    
        Vue.set(state.config.Stories, 'stories', stories);
        Vue.set(state.chaptersSortingOrder, storyId, order);
    },
    ADD_CHAPTER_SORTING_ORDER(state, {storyId, chapterId, commit}) {
        const order = state.chaptersSortingOrder[storyId] ? clone(state.chaptersSortingOrder[storyId]) : [];
        if (order.includes(chapterId)) return;
        order.push(chapterId);
        commit('UPDATE_CHAPTERS_SORTING_ORDER', {storyId, order});
    },
    SAVE_CURRENCY(state, currencyData) {
        const currencies = clone(state.config.Currencies);
        const currencyIndex = currencies.findIndex(currency => currency.id === currencyData.id);
        
        if (currencyIndex > -1)
            currencies[currencyIndex] = currencyData;
        else
            currencies.push(currencyData);
        
        Vue.set(state.config, 'Currencies', currencies);
    },
    SAVE_CATEGORY(state, categoryData) {
        const categories = clone(state.config.Categories);
        const categoryIndex = categories.findIndex(currency => currency.id === categoryData.id);
        
        if (categoryIndex > -1)
            categories[categoryIndex] = categoryData;
        else
            categories.push(categoryData);
        
        Vue.set(state.config, 'Categories', categories);
    },
    SAVE_GENRE(state, genreData) {
        const genres = clone(state.config.Genres);
        const genreIndex = genres.findIndex(genre => genre.id === genreData.id);
    
        if (genreIndex > -1)
            genres[genreIndex] = genreData;
        else
            genres.push(genreData);
    
        Vue.set(state.config, 'Genres', genres);
    },
    UPDATE_ECONOMY_CONFIG(state, data) {
        Object.keys(data).forEach(key => Vue.set(state.config.EconomyConfig, key, data[key]));
    },
    UPDATE_FAKE_STORIES(state, data) {
        if (data.fake_stories_enabled)
            Vue.set(state.config.Stories.fake_stories, 'enabled', data.fake_stories_enabled);
        if (data.fake_stories_chapters_count)
            Vue.set(state.config.Stories.fake_stories, 'chapters_count', data.fake_stories_chapters_count);
    },
    SAVE_PRICE(state, priceData) {
        const prices = clone(state.config.Prices.prices);
        const priceIndex = prices.findIndex(price => price.id === priceData.id);
        if (priceIndex > -1) prices[priceIndex] = priceData;
        else prices.push(priceData);
        Vue.set(state.config.Prices, 'prices', prices);
    },
    UPDATE_STORIES_RECOMMENDED_ORDER(state, sortOrder) {
        const stories = clone(state.config.Stories.stories);
        Object.entries(sortOrder).forEach(([storyId, order]) => {
            storyId = parseInt(storyId, 10);
            const storyIndex = stories.findIndex(story => story.id === storyId);
            if (storyId > -1) stories[storyIndex].order_recommended = order;
        });
        Vue.set(state.config.Stories, 'stories', stories);
    },
    SAVE_PRODUCT(state, productData) {
        const products = clone(state.config.Products.products);
        const productIndex = products.findIndex(product => product.id === productData.id);
        if (productIndex > -1) products[productIndex] = productData;
        else products.push(productData);
        Vue.set(state.config.Products, 'products', products);
    },
    DELETE_PRODUCT(state, productId) {
        const products = clone(state.config.Products.products);
        const productIndex = products.findIndex(product => product.id === productId);
        if (productIndex === -1) return;
        products.splice(productIndex, 1);
        Vue.set(state.config.Products, 'products', products);
    },
    UPDATE_DAILY_BONUSES_CONFIG(state, config) {
        const dailyBonuses = clone(state.config.DailyBonuses);
        Object.keys(config).forEach(key => dailyBonuses[key] = config[key]);
        Vue.set(state.config, 'DailyBonuses', dailyBonuses);
    },
    UPDATE_PRODUCTS_ORDER(state, order) {
        const products = clone(state.config.Products.products);
        const sortedProducts = [];
        Object.values(order).forEach(productId => {
            const productData = products.find(product => product.id === productId);
            if (productData) sortedProducts.push(productData);
        });
        Vue.set(state.config.Products, 'products', sortedProducts);
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
};
